@use "../utils/mixins" as *;

.title {
  color: #fff;
  text-align: center;
  font-family: Poppins, sans-serif;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  letter-spacing: -1.5px;

  @include for-tablet {
    font-size: 64px;
  }

  @include for-desktop {
    font-size: 80px;
  }

  &--secondary {
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: justify;

    @include for-tablet {
      font-size: 48px;
    }

    @include for-desktop {
      font-size: 64px;
    }
  }

  &--section {
    color: #fff;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-transform: capitalize;

    @include for-tablet {
      font-size: 48px;
      line-height: 100%;
    }

    @include for-desktop {
      font-size: 64px;
      line-height: 100%;
    }
  }

}