  @mixin for-desktop() {
    @media (min-width: 1260px) {
      @content;
    }
  }

  @mixin for-tablet() {
    @media (min-width: 744px) and (max-width: 1260px) {
      @content;
    }
  }

  @mixin grid {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(2, 130px);
    column-gap: 20px;

    @include for-tablet {
      grid-template-columns: repeat(6, 80px);
      column-gap: 24px;
    }

    @include for-desktop {
      grid-template-columns: repeat(12, 63px);
      column-gap: 24px;
    }
  }

  @mixin visually-hidden() {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
  }

  @mixin visually-visible() {
    clip: auto;
    clip-path: none;
    height: auto;
    overflow: visible;
    white-space: normal;
    width: auto;
  }